<template>
    <!--历史记录 逻辑就是搜索后的记录放到列表中，列表存到本地，然后本地记录又放在这个列表中遍历，后面搜索的也添加到这个列表中，然后这个有新内容的列表又更新到本地，又更新到列表中 闭环-->
  <van-cell-group>
    <van-cell title="历史记录" icon="sousuo">
      <!-- 删除功能 -->
      <div slot="right-icon" v-if="searchData.length !== 0">
        <i class="icon icon-shanchu" @click="setShow = false" v-if="setShow"></i>
        <div v-else class="delBtn">
          <span @click="del" class="orDel">全部删除</span>
          <span @click="setShow = true" class="okDel">完成</span>
        </div>
      </div>
    </van-cell>
    <!--展示数据-->
    <div class="lishijilu">
      <van-cell :title="item" v-for="(item, index) in searchData" :key="index" @click="setShow ? searchS(item, index) : null">
        <van-icon name="close" slot="right-icon" v-if="!setShow" @click="searchS(item, index)"/>
      </van-cell>
    </div>
  </van-cell-group>
</template>

<script>
// import { SearchDel } from '@/api/search'
import '@/store'

export default {
  name: 'lishijilu',
  data () {
    return {
      setShow: true // 控制右边显示
    }
  },
  methods: {
    // 删除二次确认并删除
    async del () {
      this.$dialog.confirm({
        message: '确认清空全部历史记录吗',
        confirmButtonColor: '#3296fa'
      })
        .then(() => {
          // this.sList = []
          this.setShow = true
          this.$emit('delAll', []) // 删除全部
        })
        .catch(() => {
          // on cancel
        })
    },
    searchS (item, index) {
      // 未在搜索状态下，点击某项进行搜索
      if (this.setShow) {
        // 把点击项传到父组件 进行搜索
        return this.$emit('getSearchItem', item)
      }
      // 删除单条
      this.$emit('delOne', index)
    }
  },
  // 当vue被创建完成时，就执行该函数中内容
  created () {},
  props: {
    // 接收搜素列表
    searchData: {
      type: Array,
      required: true
    }
  }
}
</script>

<style scoped>
  .delBtn>span{
    color: #888;
    font-size: 12px
  }
  .orDel{
    padding-right: 5px
  }
  .lishijilu{
    position: fixed;
    top: 91px;
    bottom: 0;
    overflow: scroll;
    width: 100%;
  }
</style>
