/*
* 搜索相关请求模块
*/

import request from '@/utils/request'

// 请求搜索联想
export const SearchCompletion = params => request({
  url: '/app/v1_0/suggestion',
  method: 'GET',
  params
})

// 请求用户搜索历史记录
export const SearchRecord = () => request({
  url: '/app/v1_0/search/histories',
  method: 'GET'
})

// 请求删除用户搜索历史记录(只能删除全部，不能单独删，没那接口)
export const SearchDel = () => request({
  url: '/app/v1_0/search/histories',
  method: 'DELETE'
})

// 获取搜索结果
export const SearchDatas = params => request({
  url: '/app/v1_0/search',
  method: 'GET',
  params
})
