<template>
    <!--联想建议-->
    <van-cell-group>
      <van-cell icon-prefix='icon' icon="sousuo" v-for="(item, index) in sList" :key="index" @click="getItem(item)">
        <!-- 这里需要改 -->
        <span slot='default' v-html="hightLight(item)"></span>
      </van-cell>
    </van-cell-group>
</template>

<script>
import { SearchCompletion } from '@/api/search'
// 引入lodash插件 （使用里面的函数防抖debounce）
import { debounce } from 'lodash'

export default {
  name: 'lianxiangjianyi',
  data () {
    return {
      sList: Array // 存放数据
    }
  },
  methods: {
    // 处理高亮 (在列表每一项中查找替换输入的内容，用标签样式包裹起来通过v-html挂载进去解析样式)
    hightLight (item) {
      return item.replace(
        // 这里是正则的完整写法，才可以匹配变量数据
        new RegExp(this.searchData, 'gi'),
        `<span style='color: #bf0917;'>${this.searchData}</span>`
      )
    },
    // 把点击的联想项传递给父组件处理
    getItem (item) {
      this.$emit('getSearchItem', item)
    }
  },
  props: {
    // 接收输入的内容(接收到的内容会随着输入改变而改变)
    searchData: {
      type: String,
      required: true
    }
  },
  watch: {
    // 语法糖写法，监听不到初始化时的值
    // searchData (newValue, oldValue) {
    //   console.log(111)
    // },
    // 通过watch监听属性改变，改变一次就发送请求联想数据一次！
    searchData: {
      // 监听到后 数据变化触发执行的功能
      // 改成完整函数写法后，使用debounce来嵌套住这个函数，就可以进行函数防抖（避免请求过于频繁）
      handler: debounce(async function (newValue, oldValue) {
        // 请求联想补全接口
        const { data } = await SearchCompletion({
          q: newValue
        })
        // 数据放列表中遍历
        this.sList = data.data.options
      }, 200),
      immediate: true // 控制是否监听初始化时的值（监听开始时立即调用）
    }
  }
}
</script>

