<template>
    <!--搜索结果-->
  <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="jieguo"
    >
      <van-cell v-for="(item, index) in list" :key="index" :title="item.title"  @click="$router.push(`/article/${item.art_id}`)"/>
    </van-list>
  </van-pull-refresh>
</template>

<script>
import { SearchDatas } from '@/api/search'

export default {
  name: 'sousuojieguo',
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      page: 1 // 页面
    }
  },
  methods: {
    async onLoad () {
      // 请求获取数据
      try {
        const { data } = await SearchDatas({
          page: this.page, // 页码
          pre_page: 20, // 每页多少数据
          q: this.searchData // 搜索的字符
        })
        // console.log(data)
        const results = data.data.results // 拿到数据
        this.list.push(...results) // 数据列表结构后添加进列表放入列表
        this.loading = false // 本次加载状态结束
        // 判断是否还有数据
        if (results.length) {
          this.page++ // 有数据就页码加一，好请求下一页
        } else {
          this.finished = true // 加载完成
        }
      } catch (err) {
        console.log(err)
      }
    },
    onRefresh () {
      // 清空列表数据
      this.finished = false
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true
      this.onLoad()
    }
  },
  props: {
    // 接收传过来的搜索内容
    searchData: {
      type: String,
      required: true
    }
  },
  created () {}
}
</script>

<style scoped>
  .jieguo{
    width: 100%;
    height: 622px;
    position: fixed;
    top: 45px;
    overflow: scroll;
  }
</style>
