<template>
  <div class="search">
    <navbar>
      <form action="/" slot='left'>
        <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          background='unset'
          autofocus="true"
          @search="onSearch"
          @input="onShow"
          @focus="sShow = false"
        >
          <i class='icon icon-sousuo' slot="left-icon"></i>
        </van-search>
      </form>
      <span slot='right' class='searchBtn' @click='cloneM'>取消</span>
    </navbar>
    <sousuojieguo v-if="sShow" :searchData="value"> <!--搜索结果--></sousuojieguo>
    <lianxiangjianyi v-else-if="value" :search-data="value" @getSearchItem="onSearch"><!--联想建议--></lianxiangjianyi>
    <lishijilu :search-data="sList" @getSearchItem="onSearch" @delAll="delAll" @delOne="delOne" v-else><!--历史记录--></lishijilu>
  </div>
</template>

<script>
import navbar from '@/components/navbar.vue'
import lianxiangjianyi from './components/lianxiangjianyi'
import lishijilu from './components/lishijilu'
import sousuojieguo from './components/sousuojieguo'
import '@/store'
import { setItem, getItem } from '@/utils/uUser.js'
import { SearchRecord, SearchDel } from '@/api/search.js'

export default {
  name: 'search',
  components: {
    navbar,
    lianxiangjianyi,
    lishijilu,
    sousuojieguo
  },
  data () {
    return {
      value: '', // 输入框的值
      sShow: false, // 用于各模块显示
      sList: [] // 存放搜索记录
    }
  },
  computed: {},
  watch: {
    // sList有变化就更新本地(搜索/删除单条/删除全部 的操作都会改变sList，所以就统一在这里进行更新本地存储！)
    sList () {
      setItem(this.sList, 'lishijilu')
    }
  },
  created () {
    this.getSearchData()
  },
  mounted () {},
  methods: {
    // 回车时 确认搜索 val为输入的内容
    // 点击搜索联想的项时，再次调用该函数 处理点击搜索联想项，赋值给搜索框 （val为接收的联想建议自定义事件传过来的值）
    onSearch (val) {
      this.sShow = true
      this.value = val // 处理点击搜索联想/历史记录项，赋值给搜索框

      // sList历史记录列表存储和查重
      if (this.sList !== []) {
        const sListIndex = this.sList.indexOf(this.value)
        if (sListIndex !== -1) {
          this.sList.splice(sListIndex, 1)
        }
      }
      this.sList.unshift(this.value)

      // 限制历史记录条数
      this.sList.splice(20)
    },
    // 输入内容改变时 用于判断输入框为空时显示搜索历史
    onShow () {
      if (!this.value) {
        this.sShow = false
      }
    },
    async getSearchData () {
      // 已登陆，获取服务器存储的历史记录
      if (this.$store.state.token) {
        const { data } = await SearchRecord()
        this.sList = data.data.keywords
      } else {
        // 获取本地的添加到历史记录列表
        this.sList.push(...(getItem('lishijilu') ? getItem('lishijilu') : []))
      }
    },
    // 删除全部
    delAll (i) {
      this.sList = i
      // 登陆了就请求删除全部的接口，并且更新一下登陆状态下的历史记录
      if (this.$store.state.token) {
        SearchDel()
        this.getSearchData()
      }
    },
    // 删除指定
    delOne (index) {
      this.sList.splice(index, 1) // 删除指定下标的
    },
    // 关闭搜索页面
    cloneM () {
      // 清除缓存
      this.$store.commit('removeCould', 'TabbarIndex')
      // 返回上级页面
      this.$router.back()
    }
  },
  props: {}
}
</script>

<style scoped>
  .theme-colors {
    background-color: #1989fa;
  }
  .van-search{
    width: 295px;
    height: 28px;
    padding: 0;
  }
  .van-search i{
    color: #666 !important;
  }
  .searchBtn{
    color: #fff;
    padding-right: 2px;
  }
  .icon-sousuo{
    font-size: 13px;
  }
</style>
